export default {
	data() {
		return {
			imgUrl1: '',
			imgUrl2: '',
			imgUrl3: ''
		}
	},
	methods:{
		onSubmit(){
			if (this.imgUrl1 == '' || this.imgUrl2 == '') {
				uni.showToast({
					title: '请上传身份证照片'
				})
			} else if (this.imgUrl3 == ''){
				uni.showToast({
					title: '请上传营业执照',
					icon:'none'
				})
			} else{
				uni.showToast({
					title: '提交成功'
				})
				setTimeout(()=>{
					uni.reLaunch({
						url:'business-verify'
					})
				}, 2000)
			}
			
		},
		onChooseImg1(){
			uni.chooseImage({
				count:1,
				sizeType:['original'],
				success: res=>{
					this.imgUrl1 = res.tempFilePaths[0]
					uni.request({
						url:'https://120.24.180.246/xmRepair/shopInfo/upload',
						data:{
							address:this.imgUrl1
						},
						success: res=>{
							console.log('success')
						}
					})
				}
			})
		},
		onChooseImg2(){
			uni.chooseImage({
				count:1,
				sizeType:['original'],
				success: res=>{
					this.imgUrl2 = res.tempFilePaths[0]
					uni.request({
						url:'https://120.24.180.246:8080/xmRepair/shopInfo/upload',
						data:{
							address:this.imgUrl2
						},
						success: res=>{
							console.log('success')
						}
					})
				}
			})
		},
		onChooseImg3(){
			uni.chooseImage({
				count:1,
				sizeType:['original'],
				success: res=>{
					this.imgUrl3 = res.tempFilePaths[0]
					uni.request({
						url:'https://120.24.180.246:8080/xmRepair/shopInfo/upload',
						data:{
							address:this.imgUrl3
						},
						success: res=>{
							console.log('success')
						}
					})
				}
			})
		}
	},
	
}
